import React, { useState } from 'react'
import { View, Text, Image } from '@tarojs/components'
import { AtFloatLayout, AtRate, AtTextarea, AtIcon } from 'taro-ui'
import Taro from '@tarojs/taro'
import type { OrderData } from '@/services/order'
import './index.scss'

interface ReviewModalProps {
  visible: boolean
  orderInfo: OrderData
  onClose: () => void
  onSubmit: (reviewData: {
    appointmentId: number
    rating: number
    content: string
    tags: string[]
  }) => Promise<void>
}

const ReviewModal: React.FC<ReviewModalProps> = ({
  visible,
  orderInfo,
  onClose,
  onSubmit
}) => {
  const [rating, setRating] = useState(5)
  const [content, setContent] = useState('')
  const [selectedTags, setSelectedTags] = useState<string[]>([])
  const [isSubmitting, setIsSubmitting] = useState(false)

  // Âø´Êç∑ËØÑ‰ª∑Ê†áÁ≠æ
  const quickTags = [
    'ÊâãÊ≥ï‰∏ì‰∏ö', 'ÊúçÂä°ÊÄÅÂ∫¶Â•Ω', 'ÊïàÊûúÊòæËëó',
    'ÁéØÂ¢ÉËàíÈÄÇ', 'ÂáÜÊó∂ÂÆàÁ∫¶', 'Áâ©Ë∂ÖÊâÄÂÄº'
  ]

  // Ê†πÊçÆËØÑÂàÜËé∑ÂèñÂØπÂ∫îÁöÑÊñáÊ°à
  const getRatingText = (score: number) => {
    const textMap: Record<number, string> = {
      1: 'Âæà‰∏çÊª°ÊÑè',
      2: '‰∏çÊª°ÊÑè',
      3: '‰∏ÄËà¨',
      4: 'Êª°ÊÑè',
      5: 'ÈùûÂ∏∏Êª°ÊÑè'
    }
    return textMap[score] || ''
  }

  // ÂàáÊç¢Ê†áÁ≠æÈÄâ‰∏≠Áä∂ÊÄÅ
  const toggleTag = (tag: string) => {
    setSelectedTags(prev => {
      if (prev.includes(tag)) {
        return prev.filter(t => t !== tag)
      } else {
        return [...prev, tag]
      }
    })
  }

  // Âà§Êñ≠ÊòØÂê¶ÂèØ‰ª•Êèê‰∫§
  const canSubmit = content.length >= 10 && !isSubmitting

  // Â§ÑÁêÜÊèê‰∫§
  const handleSubmit = async () => {
    if (!canSubmit) return

    setIsSubmitting(true)
    try {
      // ‰ªéorderInfo‰∏≠Ëé∑ÂèñappointmentId
      // Â¶ÇÊûúextraData‰∏≠ÊúâappointmentIdÔºå‰ΩøÁî®ÂÆÉÔºõÂê¶ÂàôÂ∞ùËØïÂÖ∂‰ªñÂ≠óÊÆµ
      const appointmentId =
        (orderInfo.extraData?.appointmentId) ||
        (orderInfo as any).appointmentId ||
        123 // ÈªòËÆ§ÂÄºÔºåÂÆûÈôÖÂ∫îËØ•‰ªéËÆ¢ÂçïÊï∞ÊçÆËé∑Âèñ

      await onSubmit({
        appointmentId,
        rating,
        content,
        tags: selectedTags
      })

      // Êèê‰∫§ÊàêÂäüÂêéÈáçÁΩÆË°®Âçï
      setRating(5)
      setContent('')
      setSelectedTags([])

      Taro.showToast({
        title: 'ËØÑ‰ª∑ÊàêÂäü',
        icon: 'success',
        duration: 2000
      })

      // Âª∂ËøüÂÖ≥Èó≠Ê®°ÊÄÅÂ±ÇÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÊàêÂäüÊèêÁ§∫
      setTimeout(() => {
        onClose()
      }, 2000)
    } catch (error: any) {
      Taro.showToast({
        title: error.message || 'ËØÑ‰ª∑Â§±Ë¥•',
        icon: 'none',
        duration: 2000
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  // Â§ÑÁêÜÂèñÊ∂à
  const handleCancel = () => {
    if (content.length > 0 || selectedTags.length > 0) {
      Taro.showModal({
        title: 'Á°ÆËÆ§ÂèñÊ∂à',
        content: 'Â∑≤Â°´ÂÜôÁöÑÂÜÖÂÆπÂ∞Ü‰∏ç‰ºö‰øùÂ≠òÔºåÁ°ÆÂÆöÂèñÊ∂àÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            // ÈáçÁΩÆË°®Âçï
            setRating(5)
            setContent('')
            setSelectedTags([])
            onClose()
          }
        }
      })
    } else {
      onClose()
    }
  }

  return (
    <AtFloatLayout
      isOpened={visible}
      title="ËØÑ‰ª∑ÊúçÂä°"
      onClose={handleCancel}
    >
      <View className="review-modal">
        {/* È°∂ÈÉ®‰ø°ÊÅØ */}
        <View className="modal-header">
          <View className="therapist-info">
            <Image
              className="avatar"
              src={orderInfo.therapistAvatar || 'https://img.yzcdn.cn/vant/cat.jpeg'}
              mode="aspectFill"
            />
            <View className="info">
              <Text className="name">{orderInfo.therapistName}</Text>
              <Text className="service">{orderInfo.serviceName}</Text>
            </View>
          </View>
        </View>

        {/* ÊòüÁ∫ßËØÑÂàÜ */}
        <View className="rating-section">
          <Text className="rating-label">ÊúçÂä°ËØÑÂàÜ</Text>
          <View className="star-container">
            <AtRate
              value={rating}
              onChange={(value) => setRating(value)}
              size={28}
              max={5}
            />
            <Text className="rating-text">{getRatingText(rating)}</Text>
          </View>
        </View>

        {/* Âø´Êç∑Ê†áÁ≠æÔºà4ÊòüÂèä‰ª•‰∏äÊòæÁ§∫Ôºâ */}
        {rating >= 4 && (
          <View className="quick-tags">
            <Text className="tags-label">Âø´ÈÄüËØÑ‰ª∑</Text>
            <View className="tags-container">
              {quickTags.map(tag => (
                <View
                  key={tag}
                  className={`tag ${selectedTags.includes(tag) ? 'active' : ''}`}
                  onClick={() => toggleTag(tag)}
                >
                  {tag}
                </View>
              ))}
            </View>
          </View>
        )}

        {/* ÊñáÂ≠óËØÑ‰ª∑ */}
        <View className="content-section">
          <Text className="content-label">ËØ¶ÁªÜËØÑ‰ª∑</Text>
          <AtTextarea
            value={content}
            onChange={(value) => setContent(value)}
            placeholder="ÂàÜ‰∫´ÊÇ®ÁöÑÊúçÂä°‰ΩìÈ™åÔºåÂ∏ÆÂä©ÂÖ∂‰ªñÈ°æÂÆ¢Ôºà1-500Â≠óÔºâ"
            maxLength={500}
            height={120}
            count
            className="content-textarea"
          />
        </View>

        {/* Â∫ïÈÉ®Êìç‰Ωú */}
        <View className="modal-footer">
          <View className="buttons">
            <View className="btn-cancel" onClick={handleCancel}>
              ÂèñÊ∂à
            </View>
            <View
              className={`btn-submit ${canSubmit ? '' : 'disabled'}`}
              onClick={handleSubmit}
            >
              {isSubmitting ? 'Êèê‰∫§‰∏≠...' : 'Êèê‰∫§ËØÑ‰ª∑'}
            </View>
          </View>
        </View>
      </View>
    </AtFloatLayout>
  )
}

export default ReviewModal