import React, { useState, useEffect } from 'react'
import { View, Text, Image } from '@tarojs/components'
import Taro, { useRouter } from '@tarojs/taro'
import { AtIcon, AtSteps } from 'taro-ui'
import { orderService, OrderData } from '@/services/order'
import { reviewService } from '@/services/review'
import { ReviewModal } from '@/components/Review'
import './index.scss'

const OrderDetailPage: React.FC = () => {
  const router = useRouter()
  const { orderNo } = router.params as { orderNo: string }
  const [orderInfo, setOrderInfo] = useState<OrderData | null>(null)
  const [loading, setLoading] = useState(true)
  const [showReviewModal, setShowReviewModal] = useState(false)
  const [hasReviewed, setHasReviewed] = useState(false)

  useEffect(() => {
    fetchOrderDetail()
  }, [orderNo])

  const fetchOrderDetail = async () => {
    if (!orderNo) {
      Taro.showToast({
        title: 'ËÆ¢ÂçïÂè∑Áº∫Â§±',
        icon: 'none'
      })
      setTimeout(() => {
        Taro.navigateBack()
      }, 1500)
      return
    }

    try {
      const order = await orderService.getOrderDetail(orderNo)
      setOrderInfo(order)

      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ËØÑ‰ª∑ÔºàÂ¶ÇÊûúÊúâappointmentIdÔºâ
      if (order.extraData?.appointmentId) {
        const canReview = await reviewService.checkCanReview(order.extraData.appointmentId)
        setHasReviewed(!canReview)
      } else {
        // Ê≤°ÊúâappointmentIdÔºåÈªòËÆ§‰∏∫Êú™ËØÑ‰ª∑
        setHasReviewed(false)
      }

      setLoading(false)
    } catch (error) {
      setLoading(false)
      Taro.showToast({
        title: 'Ëé∑ÂèñËÆ¢ÂçïÂ§±Ë¥•',
        icon: 'none'
      })
    }
  }

  const handleCall = () => {
    Taro.makePhoneCall({
      phoneNumber: '4008888888'
    })
  }

  const handleCancel = async () => {
    Taro.showModal({
      title: 'ÂèñÊ∂àËÆ¢Âçï',
      content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂àËØ•ËÆ¢ÂçïÂêóÔºü',
      success: async (res) => {
        if (res.confirm) {
          try {
            await orderService.cancelOrder(orderNo)
            Taro.showToast({
              title: 'ËÆ¢ÂçïÂ∑≤ÂèñÊ∂à',
              icon: 'success'
            })
            setTimeout(() => {
              fetchOrderDetail()
            }, 1500)
          } catch (error) {
            Taro.showToast({
              title: 'ÂèñÊ∂àÂ§±Ë¥•',
              icon: 'none'
            })
          }
        }
      }
    })
  }

  const handleRebook = () => {
    // Ë∑≥ËΩ¨Âà∞È¢ÑÁ∫¶È¶ñÈ°µÈáçÊñ∞È¢ÑÁ∫¶
    Taro.switchTab({
      url: '/pages/appointment/index'
    })
  }

  const handleCreateReview = () => {
    setShowReviewModal(true)
  }

  const handleViewReview = async () => {
    if (!orderInfo?.extraData?.appointmentId) {
      Taro.showToast({
        title: 'ÊöÇÊó†ËØÑ‰ª∑',
        icon: 'none'
      })
      return
    }

    try {
      // ‰ΩøÁî®appointmentId‰Ωú‰∏∫reviewIdÔºàÂõ†‰∏∫ÂêéÁ´Ø‰ΩøÁî®appointmentId‰Ωú‰∏∫ËØÑ‰ª∑IDÔºâ
      const reviewDetail = await reviewService.getReviewDetail(orderInfo.extraData.appointmentId)
      // ÂèØ‰ª•Ë∑≥ËΩ¨Âà∞ËØÑ‰ª∑ËØ¶ÊÉÖÈ°µÊàñÊòæÁ§∫ËØÑ‰ª∑ÂÜÖÂÆπ
      Taro.showModal({
        title: 'ÊàëÁöÑËØÑ‰ª∑',
        content: `ËØÑÂàÜÔºö${reviewDetail.rating}Êòü\n${reviewDetail.content}`,
        showCancel: false
      })
    } catch (error) {
      Taro.showToast({
        title: 'ËØÑ‰ª∑‰ø°ÊÅØËé∑ÂèñÂ§±Ë¥•',
        icon: 'none'
      })
    }
  }

  const handleSubmitReview = async (reviewData: {
    appointmentId: number
    rating: number
    content: string
    tags: string[]
  }) => {
    try {
      // Ê∑ªÂä†therapistIdÂà∞ËØÑ‰ª∑Êï∞ÊçÆ
      const fullReviewData = {
        ...reviewData,
        therapistId: orderInfo?.therapistId
      }

      await reviewService.createReview(fullReviewData)

      // Êõ¥Êñ∞ËØÑ‰ª∑Áä∂ÊÄÅ
      setHasReviewed(true)
      setShowReviewModal(false)

      // Âà∑Êñ∞ËÆ¢ÂçïËØ¶ÊÉÖ
      fetchOrderDetail()
    } catch (error: any) {
      throw error // ËÆ©ReviewModalÂ§ÑÁêÜÈîôËØØ
    }
  }

  const handleNavigate = () => {
    if (orderInfo) {
      Taro.openLocation({
        latitude: 31.189, // Á§∫‰æãÂùêÊ†á
        longitude: 121.43,
        name: orderInfo.storeName,
        address: orderInfo.storeAddress
      })
    }
  }

  const generateQRCodeUrl = (orderNo: string) => {
    return `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${orderNo}`
  }

  const getStatusText = (order: OrderData) => {
    // ‰ΩøÁî®ÁªºÂêàÊòæÁ§∫Áä∂ÊÄÅÔºà‰ºòÂÖàÔºâÊàñÊîØ‰ªòÁä∂ÊÄÅÔºàÈôçÁ∫ßÔºâ
    const status = order.displayStatus || order.paymentStatus

    const statusTextMap = {
      'pending': 'ÂæÖÊîØ‰ªò',
      'paid': 'ÂæÖÊúçÂä°',
      'serving': 'ÊúçÂä°‰∏≠',
      'completed': 'Â∑≤ÂÆåÊàê',  // üöÄ ÁÆ°ÁêÜÂëòÊ†áËÆ∞ÁöÑÂÆåÊàêÁä∂ÊÄÅ
      'cancelled': 'Â∑≤ÂèñÊ∂à',
      'refunded': 'Â∑≤ÈÄÄÊ¨æ'
    }
    return statusTextMap[status] || status
  }

  const getOrderSteps = (order: OrderData) => {
    // ‰ΩøÁî®ÁªºÂêàÊòæÁ§∫Áä∂ÊÄÅÔºà‰ºòÂÖàÔºâÊàñÊîØ‰ªòÁä∂ÊÄÅÔºàÈôçÁ∫ßÔºâ
    const status = order.displayStatus || order.paymentStatus

    const allSteps = ['‰∏ãÂçï', 'ÊîØ‰ªò', 'Âà∞Â∫óÊúçÂä°', 'ÂÆåÊàê']
    let current = 0

    switch (status) {
      case 'pending':
        current = 0
        break
      case 'paid':
        current = 1
        break
      case 'serving':
        current = 2
        break
      case 'completed':
        current = 3
        break
      case 'cancelled':
      case 'refunded':
        return { steps: ['Â∑≤ÂèñÊ∂à'], current: 0 }
    }

    return { steps: allSteps, current }
  }

  const formatDateTime = (dateStr: string) => {
    const date = new Date(dateStr)
    const year = date.getFullYear()
    const month = date.getMonth() + 1
    const day = date.getDate()
    const hour = date.getHours()
    const minute = date.getMinutes()
    return `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')} ${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
  }

  if (loading) {
    return (
      <View className="order-detail-page">
        <View className="loading">Âä†ËΩΩ‰∏≠...</View>
      </View>
    )
  }

  if (!orderInfo) {
    return (
      <View className="order-detail-page">
        <View className="empty">ËÆ¢Âçï‰∏çÂ≠òÂú®</View>
      </View>
    )
  }

  const { steps, current } = getOrderSteps(orderInfo)
  const currentStatus = orderInfo.displayStatus || orderInfo.paymentStatus

  return (
    <View className="order-detail-page">
      {/* ËÆ¢ÂçïÁä∂ÊÄÅ */}
      <View className="status-section">
        <View className="status-header">
          <AtIcon
            value={currentStatus === 'paid' ? 'check-circle' : 'clock'}
            size="40"
            color="#fff"
          />
          <Text className="status-text">{getStatusText(orderInfo)}</Text>
        </View>

        {/* ËÆ¢ÂçïÊµÅÁ®ã */}
        {currentStatus !== 'cancelled' && currentStatus !== 'refunded' && (
          <View className="steps-container">
            <AtSteps
              items={steps.map(step => ({ title: step }))}
              current={current}
              className="order-steps"
            />
          </View>
        )}
      </View>

      {/* Ê†∏ÈîÄ‰∫åÁª¥Á†ÅÔºàÂ∑≤ÊîØ‰ªòÁä∂ÊÄÅÊòæÁ§∫Ôºâ */}
      {currentStatus === 'paid' && (
        <View className="qrcode-section">
          <Text className="section-title">Âà∞Â∫óÊ†∏ÈîÄÁ†Å</Text>
          <View className="qrcode-card">
            <Image 
              className="qrcode-image" 
              src={generateQRCodeUrl(orderInfo.orderNo)}
              mode="aspectFit"
            />
            <Text className="qrcode-no">{orderInfo.orderNo}</Text>
            <Text className="qrcode-tip">ËØ∑ÂêëÈó®Â∫óÂ∑•‰Ωú‰∫∫ÂëòÂá∫Á§∫Ê≠§‰∫åÁª¥Á†Å</Text>
          </View>
        </View>
      )}

      {/* ËÆ¢Âçï‰ø°ÊÅØ */}
      <View className="info-section">
        <Text className="section-title">ËÆ¢Âçï‰ø°ÊÅØ</Text>
        <View className="info-card">
          <View className="info-item">
            <Text className="label">ËÆ¢ÂçïÁºñÂè∑</Text>
            <Text className="value">{orderInfo.orderNo}</Text>
          </View>
          <View className="info-item">
            <Text className="label">‰∏ãÂçïÊó∂Èó¥</Text>
            <Text className="value">{formatDateTime(orderInfo.createdAt)}</Text>
          </View>
          {orderInfo.paidAt && (
            <View className="info-item">
              <Text className="label">ÊîØ‰ªòÊó∂Èó¥</Text>
              <Text className="value">{formatDateTime(orderInfo.paidAt)}</Text>
            </View>
          )}
          <View className="info-item">
            <Text className="label">È¢ÑÁ∫¶Êó∂Èó¥</Text>
            <Text className="value highlight">
              {orderInfo.appointmentDate} {orderInfo.appointmentTime}
            </Text>
          </View>
        </View>
      </View>

      {/* Èó®Â∫ó‰ø°ÊÅØ */}
      <View className="store-section">
        <Text className="section-title">Èó®Â∫ó‰ø°ÊÅØ</Text>
        <View className="store-card" onClick={handleNavigate}>
          <View className="store-info">
            <Text className="store-name">{orderInfo.storeName}</Text>
            <Text className="store-address">{orderInfo.storeAddress}</Text>
          </View>
          <AtIcon value="map-pin" size="20" color="#a40035" />
        </View>
      </View>

      {/* Êé®ÊãøÂ∏à‰ø°ÊÅØ */}
      <View className="therapist-section">
        <Text className="section-title">Êé®ÊãøÂ∏à‰ø°ÊÅØ</Text>
        <View className="therapist-card">
          <Image 
            className="therapist-avatar" 
            src={orderInfo.therapistAvatar || 'https://img.yzcdn.cn/vant/cat.jpeg'} 
          />
          <View className="therapist-info">
            <Text className="therapist-name">{orderInfo.therapistName}</Text>
            <Text className="service-name">{orderInfo.serviceName}</Text>
            <Text className="service-duration">ÊúçÂä°Êó∂ÈïøÔºö{orderInfo.duration}ÂàÜÈíü</Text>
          </View>
          <Text className="price">¬•{orderInfo.totalAmount}</Text>
        </View>
      </View>

      {/* Êìç‰ΩúÊåâÈíÆ */}
      <View className="action-section">
        {currentStatus === 'paid' && (
          <>
            <View className="button secondary" onClick={handleCall}>
              <AtIcon value="phone" size="18" />
              <Text>ËÅîÁ≥ªÈó®Â∫ó</Text>
            </View>
            <View className="button danger" onClick={handleCancel}>
              ÂèñÊ∂àËÆ¢Âçï
            </View>
          </>
        )}
        {/* ÊúçÂä°‰∏≠ÔºöÂèØ‰ª•ËÅîÁ≥ªÈó®Â∫ó */}
        {currentStatus === 'serving' && (
          <View className="button secondary" onClick={handleCall}>
            <AtIcon value="phone" size="18" />
            <Text>ËÅîÁ≥ªÈó®Â∫ó</Text>
          </View>
        )}
        {/* Â∑≤ÂÆåÊàêÁä∂ÊÄÅÊàñÂ∑≤ÊîØ‰ªòÁä∂ÊÄÅÔºöÊòæÁ§∫ËØÑ‰ª∑ÂíåÂÜçÊ¨°È¢ÑÁ∫¶ÊåâÈíÆ */}
        {(currentStatus === 'completed' || currentStatus === 'paid') && (
          <>
            {!hasReviewed ? (
              <View className="button primary" onClick={handleCreateReview}>
                <AtIcon value="star" size="16" />
                <Text> ËØÑ‰ª∑ÊúçÂä°</Text>
              </View>
            ) : (
              <View className="button secondary" onClick={handleViewReview}>
                Êü•ÁúãËØÑ‰ª∑
              </View>
            )}
            <View className="button primary" onClick={handleRebook}>
              ÂÜçÊ¨°È¢ÑÁ∫¶
            </View>
          </>
        )}
        {/* Â∑≤ÂèñÊ∂à/Â∑≤ÈÄÄÊ¨æÔºöÂè™ÊòæÁ§∫ÂÜçÊ¨°È¢ÑÁ∫¶ */}
        {(['cancelled', 'refunded'].includes(currentStatus)) && (
          <View className="button primary" onClick={handleRebook}>
            ÂÜçÊ¨°È¢ÑÁ∫¶
          </View>
        )}
      </View>

      {/* ËØÑ‰ª∑Ê®°ÊÄÅÂ±Ç */}
      {orderInfo && (
        <ReviewModal
          visible={showReviewModal}
          orderInfo={orderInfo}
          onClose={() => setShowReviewModal(false)}
          onSubmit={handleSubmitReview}
        />
      )}
    </View>
  )
}

export default OrderDetailPage